.main {
  padding: 10px;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.layout {
  display: flex;
  height: 100%;
}

.sidebar {
  height: 100%;
  width: 230px;
  border-radius: 5px;
  background-color: rgb(2, 32, 116);
  color: white;
  box-shadow: 0 0 2px gray;
  transition: width 0.3s ease;
}

.sidebar.collapsed {
  width: 80px;
}

.collapse-btn {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  margin: 10px;
  font-size: 1rem;
}

.content {
  flex: 1;
  height: 100%;
  margin-left: 7px;
}

.header {
  height: 10vh;
  margin-bottom: 20px;
  box-shadow: 0 0 2px gray;
  background-color: rgb(255, 254, 254);
}

.body {
  flex: 1;
  margin-bottom: 20px;
  box-shadow: 0 0 2px gray;
  background-color: rgb(255, 254, 254);
  overflow-y: auto;
}

.logo h4 {
  font-size: 1.5rem;
  text-align: center;
  margin: 20px 0px;
}

.menu {
  margin-top: 32px;
}

.menu-item {
  margin-top: 30px;
  padding: 10px;
  display: flex;
  align-items: center;
  transition: background-color 0.3s ease;
}

.menu-item a {
  color: white;
  text-decoration: none;
  font-size: large;
  display: flex;
  align-items: center;
}

.menu-item i {
  font-size: 0.95rem;
  margin: 0 15px;
}

.menu-item:hover {
  background-color: rgba(255, 255, 255, 0.1);
  cursor: pointer;
}

.active {
  background-color: white;
  cursor: pointer;
}

.active a, .active i {
  color: rgb(2, 32, 116);
}

.header-content {
  margin: 0 15px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 70px;
}

.header-content i {
  margin-right: 20px;
  font-size: 1.5rem;
}

.header-content a {
  text-decoration: none;
  color: black;
  margin: 0 10px;
  text-transform: uppercase;
  font-size: 1.2rem;
}

/* Responsive styles */
@media (max-width: 749px) {
  .layout {
    flex-direction: column;
  }
  .menu-item {
  margin-top: 5px;}

  .sidebar {
    width: 100%;
    height: auto;
  }

  .sidebar.collapsed {
    width: 100%;
  }

  .collapse-btn {
    display: none;
  }

  .menu-item {
    justify-content: center;
  }
}

@media (max-width: 576px) {
  .header-content {
    flex-direction: column;
    
  }
  .menu-item {
  margin-top: 0px;
  }
  .header-content a {
    margin-top: 5px;
  }

  .sidebar {
    width: 100%;
  }

  .menu-item a {
    font-size: medium;
  }
}
